import gsap from "gsap";
import { useGSAP } from "@gsap/react";

const text = () => {
  useGSAP(() => {
    gsap.from(".fade-in-text", {
      opacity: 0, // Начать с прозрачности 0
      duration: 1.5, // Длительность анимации 1.5 секунды
      delay: 0.5, // Задержка перед началом анимации 0.5 секунды
      ease: "power2.out", // Функция сглаживания (как элемент ускоряется/замедляется)
    });
  });
  return (
    <div>
      <h1 class="fade-in-text">Привет, мир!</h1>
    </div>
  );
};

export default text;
